.sidebarStyle {
  display: inline-block;
  position: absolute;
  top: 0;
  left: 0;
  margin: 12px;
  background-color: #404040;
  color: #ffffff;
  z-index: 1 !important;
  padding: 6px;
  font-weight: bold;
}

.mapContainer.mapboxgl-map {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}

.marker {
  // background-image: url('mapbox-icon.png');
  // background-size: cover;

  background-color: #f00;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  cursor: pointer;
}

#fit {
  z-index: 1 !important;

  display: block;
  position: relative;
  margin: 0px auto;
  width: 50%;
  height: 40px;
  padding: 10px;
  border: none;
  border-radius: 3px;
  font-size: 12px;
  text-align: center;
  color: #fff;
  background: #ee8a65;
}

.header {
  z-index: 1 !important;

  display: block;
  position: relative;
  margin: 0px auto;
}